@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
body{margin:0}

.container{background-color:#3F681C;height:calc(100% - 80px);padding:20px;text-align:center}.container h1{color:white;font-family:'Open Sans', sans-serif;font-weight:700;text-transform:uppercase}.container .container-img{display:flex;flex-wrap:wrap;justify-content:center;width:100%}@media (min-width: 768px){.container .container-img{margin:0 auto;width:40%}}@media (min-width: 1200px){.container .container-img{width:25%}}.container .container-img figure{background-color:#fff;box-shadow:1px 2px 3px black;margin:15px auto;padding:5px;width:100%}.container .container-img figure img{height:50vh;width:100%}.container button{background-color:#FFBB00;border:none;color:#fff;cursor:pointer;font-family:'Open Sans', sans-serif;font-size:16px;letter-spacing:3px;outline:none;padding:10px 30px;transition:.5s ease-in-out;width:100%}@media (min-width: 768px){.container button{width:40%}}@media (min-width: 1200px){.container button{width:25%}}.container button:hover{background-color:#fff;color:#FFBB00}

footer{align-items:center;background-color:#FFBB00;display:flex;font-family:'Open Sans', sans-serif;font-size:13px;height:40px;justify-content:center}

.app{height:100vh}

